.footer {
  background: #242424;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
}

.footer_text {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
}

.footer_link {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #FFFFFF;
  margin-top: 15px;

  &:hover {
    cursor: pointer;
    color: #4A50ED;
  }
}

.footer {
  padding: 80px 0;
}

.footer_developer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: white;
  margin-top: 15px;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    color: #4A50ED;

    & .footer_logo {
      fill: #4A50ED;
    }
  }
}

.footer_logo {
  margin-left: 4px;
}