.main {
  flex-grow: 1;
  padding-top: calc(50px + 80px);
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
  background: url("../img/background.svg");
  background-size: cover;
}

.main_img {
  margin-bottom: 20px;
}

.main_title {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-bottom: 25px;
}

.main_subtitle {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 140%;
  text-align: center;
  color: #11244B;
  margin-bottom: 40px;

  & span {
    font-weight: bold;
  }
}

.main_description {
  font-family: 'Ubuntu', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #11244B;
  margin-bottom: 10px;

  & a {
    color: #11244B;

    &:hover {
      color: #603EE6;
    }
  }
}

@media(max-width: 767px) {
  .main {
    padding-top: calc(40px + 60px);
    padding-bottom: 40px
  }

  .main_img {
    width: 48px;
    height: 48px;
    margin-bottom: 16px;
  }

  .main_title {
    font-size: 26px;
    line-height: 120%;
    margin-bottom: 16px;

    & br {
      display: none;
    }
  }

  .main_subtitle {
    font-size: 18px;
    line-height: 140%;
    margin-bottom: 50px;
  }

  .main_description {
    font-size: 15px;
    line-height: 120%;
    margin-bottom: 12px;
  }
}